// 兼容

import ClipboardManager from '@ali/clipboard-manager';
import {
  biFactory,
  ClipboardPad,

  // actions
  CLIPBOARD_INSERT_FRAGMENT,
  CLIPBOARD_SET_DECORATIONS,
  CLIPBOARD_DEL_SELECTION,
  CLIPBOARD_HIDE_PASTEPANEL,
  CLIPBOARD_HIDE_CUSTOM_PASTEPANEL,
  CLIPBOARD_MATCH_MODE,
  CLIPBOARD_TEXT_MODE,
  insertFragment,
  setDecorations,
  deleteAtRange,
  hidePastePanel,
  hideCustomPastePanel,
  insertMatchFragment,
  insertTextFragment,

  // handler...
  utils,
  createOnCangjieCopyOrCut,
} from '@ali/4ever-plugin-clipboard';
import type { BiPluginConfig } from '@ali/4ever-cangjie';

const createClipboardPlugin = (configs: BiPluginConfig) => biFactory(configs);

const actions = {
  CLIPBOARD_INSERT_FRAGMENT,
  CLIPBOARD_SET_DECORATIONS,
  CLIPBOARD_DEL_SELECTION,
  CLIPBOARD_HIDE_PASTEPANEL,
  CLIPBOARD_HIDE_CUSTOM_PASTEPANEL,
  CLIPBOARD_MATCH_MODE,
  CLIPBOARD_TEXT_MODE,
  insertFragment,
  setDecorations,
  deleteAtRange,
  hidePastePanel,
  hideCustomPastePanel,
  insertMatchFragment,
  insertTextFragment,
};

createClipboardPlugin.actions = actions;
createClipboardPlugin.ClipboardPad = ClipboardPad;
createClipboardPlugin.utils = {
  ...utils,
  createOnCangjieCopyOrCut,
};
createClipboardPlugin.ClipboardManager = ClipboardManager;

export default createClipboardPlugin;
